.charity_header {
  color: white;
  font-size: 2.5vw;
  font-family: "Microsoft YaHei", 微软雅黑, "MicrosoftJhengHei", 华文细黑,
    STHeiti, MingLiu;
  text-align: center;
  background-color: #c0087f;
  margin-top: 4vw;
  margin-bottom: 3vw;
}

.search-container {
  text-align: center;
}

.search-container img {
  max-width: 12%;
  width: 12%;
  max-height: 20%;
  height: 20%;
  margin-bottom: 2vw;
}

.search-container input[type="text"] {
  margin-top: 0.4vw;
  font-size: 1.4vw;
}

.search-container button {
  font-family: Arial, Helvetica, sans-serif;
  margin-top: 0.2vw;
  background: #ddd;
  font-size: 1.5vw;
  border: none;
  cursor: pointer;
}

.search-container button:hover {
  background: #ccc;
}

.charityListHeader {
  text-indent: 3vw;
  font-size: 2vw;
  color: #c0087f;
  text-align: center;
  margin-top: 3vw;
}
